.homepage{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding:20px 80px;
}
.categories-container{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}
.category-item{
    min-width: 30%;
    height: 240px;
    flex:1 1 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    margin:0 7.5px 15px;
}

.content-holder{
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border:1px solid black;
}
.title{
    font-weight: bold;
    margin-bottom:6px;
    font-size:22px;
    color:#4a4a4a;
}
.subtitle{
    font-weight: lighter;
    font-size: 16px;
}